<template>
  <div>

    <div class="text-h6 text-md-h5 text-truncate">{{ session.title }}</div>

    <v-progress-linear :value="progress" class="mb-5"></v-progress-linear>

    <div>  <!--class="d-flex justify-center"-->
      <component
        :is="session.exercices[currentExoIdx].type"
        :exercice="session.exercices[currentExoIdx]"
      />
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import qcm from '@/components/Exos/qcm.vue'
import question from '@/components/Exos/question.vue'
import translate from '@/components/Exos/translate.vue'

export default {
  name: 'Session',
  props: {
    id: {
      required: true
    }
  },
  created() {
    this.resetCurrent()
    this.setCurrentSession(this.id)
    this.setCurrentExo(0)
  },
  data() {
    return {
    }
  },
  components: {
    qcm, question, translate
  },
  methods: {
    ...mapMutations([
      'setCurrentSession', 'setCurrentExo', 'resetCurrent'
    ]),
  },
  computed: {
    session () {
      return this.getSession(this.id)
    },
    progress () {
      if (this.currentExoIdx === this.session.exercices.length) {
        (new Audio(require('@/assets/sounds/finish.mp3'))).play();
      }
      return (this.currentExoIdx)*100 / this.session.exercices.length
    },
    ...mapGetters({
      getSession: 'getSession',
      currentExoIdx: 'getCurrentExoIdx'
    }),
  }
}
</script>
