<template>
  <div class="exo questions">
    <v-form>

      <v-text-field
        :label="exercice.question"
        v-model="answers[exercice.id]"
        :success-messages="(showCorrection && answers[exercice.id]==exercice.response) ? exercice.response : ''"
        :error-messages="showCorrection && answers[exercice.id]!=exercice.response ? exercice.response : ''"
        :readonly="showCorrection"
      >
      </v-text-field>

      <v-btn color="primary" class="mt-4" @click="submit">
        {{ !this.showCorrection ? 'Valider' : 'Exercice suivant' }}
      </v-btn>

    </v-form>
  </div>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex'

  export default {
    props: {
      exercice: {
        required: true
      }
    },
    data() {
      return {
        answers: [],
        showCorrection: false,
      }
    },
    methods: {
      submit(evt) {
        if (!this.showCorrection) {
          this.showCorrection=true
        } else {
          this.incCurrentExo()
        }
      },
      ...mapMutations([
        'incCurrentExo'
      ]),
    },
    computed: {
    }
  }
</script>
