<template>
  <div class="exo qcm">
    <v-form>

      <v-radio-group v-model="answers[exercice.id]">
        <template v-slot:label>
          <div class="text-body-1 font-weight-bold">{{ exercice.question }}</div>
        </template>
        <v-radio
          v-for="(answer, answerIdx) in exercice.answers" :key="answerIdx"
          :value="answerIdx"
          :disabled="showCorrection"
        >
          <template v-slot:label>
            <div :class="getAnswerStyle(answerIdx)">{{ answer.label }}</div>
          </template>
        </v-radio>
      </v-radio-group>

      <v-btn
        color="primary"
        class="mr-4"
        @click="submit"
      >
        {{ !this.showCorrection ? 'Valider' : 'Exercice suivant' }}
      </v-btn>

    </v-form>
  </div>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex'

  export default {
    props: {
      exercice: {
        required: true
      }
    },
    data() {
      return {
        answers: [],
        showCorrection: false,
      }
    },
    methods: {
      submit(evt) {
        if (!this.showCorrection) {
          this.showCorrection=true
          this.playSound();
        } else {
          this.showCorrection=false
          this.incCurrentExo()
        }
        // alert(JSON.stringify(this.answers))
      },
      reset(evt) {
        this.answers = []
        this.showCorrection = false
      },
      isCorrect() {
        const userAnswer = this.answers[this.exercice.id],
          answer = this.exercice.answers[userAnswer]

        if(Number.isInteger(userAnswer) && answer.correct) {
          return true;
        }
        return false;
      },
      playSound() {
        if (this.isCorrect()) {
          (new Audio(require('@/assets/sounds/ok.mp3'))).play();
        } else {
          (new Audio(require('@/assets/sounds/error.mp3'))).play();
        }
      },
      getAnswerStyle (answerIdx) {
        if (this.showCorrection) {
          const answer = this.exercice.answers[answerIdx],
            userAnswer = this.answers[this.exercice.id]

          if (answer.correct) {
            // correct answer
            return "font-weight-bold green--text"
          } else if(Number.isInteger(userAnswer) && userAnswer == answerIdx && this.exercice.answers[userAnswer].correct!==true) {
            // error
            return "font-weight-bold red--text"
          }
        }

        return "font-weight-normal"
      },
      ...mapMutations([
        'incCurrentExo'
      ]),
    }
  }
</script>
