<template>
  <div class="exo translate">
    <v-form>

      <div class="text-h5 font-weight-bold mb-3">Traduis en {{ exercice.options.to }}</div>
      <div class="text-body-1 font-weight-bold mb-3">
        <v-icon class="primary--text" @click="speech" v-if="canSpeech">mdi-volume-high</v-icon>
        "{{ exercice.question }}"
      </div>

      <v-text-field
        label="réponse"
        v-model="answers[exercice.id]"
        :success-messages="(showCorrection && answers[exercice.id]==exercice.response) ? exercice.response : ''"
        :error-messages="showCorrection && answers[exercice.id]!=exercice.response ? exercice.response : ''"
        :readonly="showCorrection"
      >
      </v-text-field>

      <v-btn color="primary" class="mt-4" @click="submit">
        {{ !this.showCorrection ? 'Valider' : 'Exercice suivant' }}
      </v-btn>

    </v-form>
  </div>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex'

  export default {
    props: {
      exercice: {
        required: true
      }
    },
    data() {
      return {
        answers: [],
        showCorrection: false,
      }
    },
    methods: {
      submit(evt) {
        if (!this.showCorrection) {
          this.showCorrection=true
        } else {
          this.incCurrentExo()
        }
      },
      speech() {
        let msg = new SpeechSynthesisUtterance();
        msg.voice = this.getVoice()
        msg.volume = 1; // From 0 to 1
        msg.rate = 0.6; // From 0.1 to 10
        msg.pitch = 1; // From 0 to 2
        msg.text = this.exercice.question;
        msg.lang = this.exercice.options.from;
        speechSynthesis.speak(msg);
      },
      getVoice() {
        const voices = this.getVoices();
        // console.log(voices)
        return voices.find(ssv => ssv.lang.split('-')[0] === this.exercice.options.from && ssv.localService === true)
      },
      ...mapGetters(['getVoices']),
      ...mapMutations(['incCurrentExo']),
    },
    computed: {
      canSpeech() {
        const hasAPI = 'speechSynthesis' in window && 'SpeechSynthesisUtterance' in window
        return !!(hasAPI && this.getVoice())
      }

    }
  }
</script>
